/* eslint-disable prefer-rest-params */
const universalChange = type => onChange =>
  function doChange() {
    switch (type.toLowerCase()) {
      case 'input': {
        const [e] = arguments;
        const {
          target: { value, name },
        } = e;
        onChange({ value, name });
        break;
      }
      case 'checkbox': {
        const [checked, e] = arguments;
        onChange({ value: checked, name: e.target.name });
        break;
      }
      case 'datepicker': {
        const [name, value] = arguments;
        onChange({ value, name });
        break;
      }
      case 'tabs': {
        const [value] = arguments;
        onChange({ value, name: 'activeTab' });
        break;
      }
      default: {
        onChange(...arguments);
        break;
      }
    }
  };

export default universalChange;
