/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React, { Component } from 'react';
import {
  func,
  string,
  arrayOf,
  objectOf,
  instanceOf,
  object,
  bool,
} from 'prop-types';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { withStyles } from 'components/WithStyle';
import universalChange from 'utils/universalChange';
import {
  DatePicker,
  Input,
  Tabs,
  Tab,
  Toggle,
  Checkbox,
  Modal,
} from 'homecredit-ui';
import Layout from 'components/pz/PzLayout';
import style from './CompetitionReport.css';
import {
  initReport,
  changeData,
  changeTabData,
  switchTab,
  changeCompetitorData,
  closeModal,
  submit,
} from './action';
import { COMPETITORS, MESSAGES, LABELS } from './constants';

class CompetitionReport extends Component {
  static propTypes = {
    doInitReport: func.isRequired,
    doSubmit: func.isRequired,
    doCloseModal: func.isRequired,
    doChangeData: func.isRequired,
    doChangeTabData: func.isRequired,
    doSwitchTab: func.isRequired,
    doChangeCompetitorData: func.isRequired,
    reportDate: instanceOf(Date).isRequired,
    activeTab: string.isRequired,
    tabData: objectOf(object).isRequired,
    errors: objectOf(object).isRequired,
    assignedShops: arrayOf(object).isRequired,
    isAvailable: bool.isRequired,
    isModalOpen: bool.isRequired,
  };

  componentDidMount() {
    const { doInitReport, assignedShops } = this.props;
    const test = [
      {
        shopCode: '000001',
        shopName: 'Cửa hàng 1',
      },
      {
        shopCode: '000002',
        shopName: 'Cửa hàng 2',
      },
      {
        shopCode: '000003',
        shopName: 'Cửa hàng 3',
      },
      {
        shopCode: '000004',
        shopName: 'Cửa hàng 4',
      },
      {
        shopCode: '000005',
        shopName: 'Cửa hàng 5',
      },
    ];
    doInitReport(test);
  }

  handleChange = ({ value, name }) => {
    this.props.doChangeData({ value, name });
  };

  handleTabChange = ({ value, name }) => {
    const { doChangeTabData, activeTab } = this.props;
    doChangeTabData({
      tabId: activeTab,
      name,
      value,
    });
  };

  handleCompetitorChange = type => competitor =>
    universalChange(type)(({ name, value }) => {
      const { doChangeCompetitorData, activeTab } = this.props;
      doChangeCompetitorData({
        tabId: activeTab,
        competitor,
        name,
        value,
      });
    });

  // wrap onChange event of components for consistent arguments: onChange({value, name})
  handleCheckboxChange = universalChange('checkbox')(this.handleChange);
  handleDateChange = universalChange('datepicker')(this.handleChange);
  handleTabCheckboxChange = universalChange('checkbox')(this.handleTabChange);
  handleTabInputChange = universalChange('input')(this.handleTabChange);
  handleCompetitorCheckboxChange = this.handleCompetitorChange('checkbox');
  handleCompetitorInputChange = this.handleCompetitorChange('input');

  render() {
    const {
      reportDate,
      tabData,
      isAvailable,
      activeTab,
      isModalOpen,
      doCloseModal,
      doSubmit,
      doSwitchTab,
      errors,
    } = this.props;
    const tabKeys = Object.keys(tabData).sort();

    return (
      <Layout
        hideBackButton
        onNextClick={doSubmit}
        nextText={LABELS.SUBMIT}
        className={style.layout}
      >
        <DatePicker
          name="reportDate"
          label={LABELS.DATE_REPORT}
          onChange={this.handleDateChange}
          value={reportDate}
          maxDate={new Date()}
          touched={false}
          errorMsg=""
          helpIcon={false}
          leftIcon
          require
        />
        <div className={style.inlineToogle}>
          <Toggle
            name="isAvailable"
            checked={isAvailable}
            onChange={this.handleCheckboxChange}
          />
          <div>{isAvailable ? MESSAGES.AVAILABLE : MESSAGES.UNAVAILABLE}</div>
        </div>
        {tabKeys.length > 0 && (
          <React.Fragment>
            <div>
              <Tabs default={tabKeys[0]} onClick={doSwitchTab}>
                {tabKeys.map(key => (
                  <Tab
                    key={key}
                    active={activeTab}
                    label={key}
                    disabled={!isAvailable}
                    error={!!errors[key]}
                  >
                    {tabData[key].title}
                  </Tab>
                ))}
              </Tabs>
            </div>
            <div className={style.tabRow}>
              <Input
                label={LABELS.SHOP_CODE}
                name="shopCode"
                value={tabData[activeTab].shopCode}
                touched={false}
                errorMsg=""
                required
                onChange={this.handleTabDataChange}
                disabled
                require
              />
              <Input
                label={LABELS.SHOP_NAME}
                name="shopName"
                value={tabData[activeTab].shopName}
                touched={false}
                errorMsg=""
                required
                onChange={this.handleTabDataChange}
                disabled
                require
              />
            </div>
            <div className={style.inlineToogle}>
              <Toggle
                checked={tabData[activeTab].hasFinancialPartner}
                onChange={this.handleTabCheckboxChange}
                name="hasFinancialPartner"
                disabled={!isAvailable}
              />
              <div>
                {tabData[activeTab].hasFinancialPartner
                  ? MESSAGES.HAS_FINANCIAL_PARTNER
                  : MESSAGES.HASNOT_FINANCIAL_PARTNER}
              </div>
            </div>
            {COMPETITORS.map(competitor => (
              <div className={style.competitorInfo} key={competitor}>
                <Checkbox
                  label={competitor}
                  checked={
                    tabData[activeTab].competitors[competitor]
                      .selectedCompetitor
                  }
                  name="selectedCompetitor"
                  onChange={this.handleCompetitorCheckboxChange(competitor)}
                  disabled={
                    !isAvailable || !tabData[activeTab].hasFinancialPartner
                  }
                />
                <Input
                  placeholder={LABELS.QTY_CONTRACT}
                  value={
                    tabData[activeTab].competitors[competitor].qtyOfContract
                  }
                  name="qtyOfContract"
                  onChange={this.handleCompetitorInputChange(competitor)}
                  disabled={
                    !isAvailable || !tabData[activeTab].hasFinancialPartner
                  }
                />
              </div>
            ))}
            <Input
              label={LABELS.OTHER_COMPETITORS}
              name="otherCompetitors"
              value={tabData[activeTab].otherCompetitors}
              touched={false}
              errorMsg=""
              onChange={this.handleTabInputChange}
              className={style.otherCompetitors}
              disabled={!isAvailable}
            />
          </React.Fragment>
        )}
        <Modal
          isOpen={isModalOpen}
          footer={
            <div className={style.modalFooter} onClick={doCloseModal}>
              <h4>OK</h4>
            </div>
          }
        >
          <p className={style.errorText}>
            {MESSAGES.UNCOMPLETED_ERROR(
              Object.keys(errors)
                .map(id => tabData[id].shopCode)
                .join(', '),
            )}
          </p>
        </Modal>
      </Layout>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  doInitReport(payload) {
    dispatch(initReport(payload));
  },
  doChangeData(payload) {
    dispatch(changeData(payload));
  },
  doChangeTabData(payload) {
    dispatch(changeTabData(payload));
  },
  doSwitchTab(payload) {
    dispatch(switchTab(payload));
  },
  doChangeCompetitorData(payload) {
    dispatch(changeCompetitorData(payload));
  },
  doCloseModal() {
    dispatch(closeModal());
  },
  doSubmit() {
    dispatch(submit());
  },
});

const mapStateToProps = state => ({
  ...state.pages.report,
  ...state.pages.login.userInfo,
});

const enhance = compose(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  ),
  withStyles(style),
);

export default enhance(CompetitionReport);
